.mkd-header-vertical-closed {
    .menu-item .second {
        @include mkdTransition(opacity .1s);
    }
    .menu-item .second.mkd-float-open {
        margin-left: 0px;
        opacity: 1;
        visibility: visible;
        overflow: visible;
        @include mkdTransition(opacity .25s ease-out);
    }
}